</template>
<script>
    export default { 
        data() {
            return {
                karton: [],
                kartond: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'konsinyasis/cetaksuratjalan/' + this.$route.params.kode
                this.$http.get(uri).then(response => {
                    this.karton = response.data
                    
                    for(var i in this.karton){
                        this.karton[i]["No"] = parseFloat(i)+parseFloat(1)
                        this.karton[i]["JumlahRupiah"] =  this.karton[i]["Qty"]*this.karton[i]["HrgJualOutLet"]
                    }
                    this.karton["Tanggal"] = response.data[0]["Tanggal"]
                    this.karton["NoSrtJalan"] = response.data[0]["NoSrtJalan"]
                    this.karton["path"] = response.data[0]["path"]
                    this.karton["pathQR"] = response.data[0]["pathQR"]
                    this.karton["Nama Pelanggan"] = response.data[0]["Nama Pelanggan"]
                    this.karton["Alamat Pelanggan"] = response.data[0]["Alamat Pelanggan"]
                    this.karton["KotaKirim"] = response.data[0]["KotaKirim"]
                    this.karton["Kode Pos Pelanggan"] = response.data[0]["Kode Pos Pelanggan"]
                    this.karton["Nama Langganan"] = response.data[0]["Nama Langganan"]
                    this.karton["Alamat"] = response.data[0]["Alamat"]
                    this.karton["Kota"] = response.data[0]["Kota"]
                    
                })
            },

            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style scoped>
    @media print {
        #print {
